<template>
<div class="d-flex flex-column w-100 h-100">
    <div class="d-flex flex-column w-100 h-100 sign-in" v-if="hasLoggedIn">
        <div class="d-flex flex-column justify-content-center align-items-center flex-grow-1 px-5">
            <img class="logo d-block mx-auto mb-5" src="../assets/images/icon.svg" width="48">
            <div class="h3 mb-1">
                Welcome back.
            </div>
            <div class="lead text-muted">
                Let's get you signed in.
            </div>
        </div>
        <div class="d-flex flex-column px-5 pb-5">
            <b-button pill variant="primary" class="w-100" @click.prevent="openExternal(signInUrl)">
                Sign In
            </b-button>
            <div class="font-weight-bold text-center small mt-4">
                Don't have an account?
                <div><a href="#" @click.prevent="openExternal(signUpUrl)" class="text-primary">Create one for free</a></div>
            </div>
        </div>
    </div>
    <div class="d-flex flex-column w-100 h-100 sign-in" v-else>
        <div class="d-flex flex-column justify-content-center align-items-center flex-grow-1 px-5">
            <img class="logo d-block mx-auto mb-5" src="../assets/images/icon.svg" width="48">
            <div class="h3 mb-1">
                Hey, welcome!
            </div>
            <div class="lead text-muted">
                Create a free account to start using Amino.
            </div>
        </div>
        <div class="d-flex flex-column px-5 pb-5">
            <b-button pill variant="primary" class="w-100" @click.prevent="openExternal(signUpUrl)">
                Sign Up
            </b-button>
            <div class="font-weight-bold text-center small mt-4">
                Already have an account?
                <div><a href="#" @click.prevent="openExternal(signInUrl)" class="text-primary">Sign in here</a></div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
export default {
    name: 'SignIn',
    data() {
        return {
            hasLoggedIn: false
        };
    },
    computed: {
        signInUrl() {
            return process.env.VUE_APP_AMINO_APP_URL + '/sign-in?context=extension';
        },
        signUpUrl() {
            return process.env.VUE_APP_AMINO_APP_URL + '/create-account?context=extension';
        }
    },
    async beforeMount() {
        this.hasLoggedIn = await new Promise(resolve => {
            this.$chrome.storage.sync.get(['___extension__flags_has_logged_in'], result => {
                resolve(result.___extension__flags_has_logged_in || false);
            });
        });
    }
}
</script>

<style lang="scss" scoped>
.sign-in {
    background-color: var(--color-950);
    background: url("../assets/images/sign-in-bg.jpg") center top no-repeat;
    background-size: 100%;
}
</style>
